import React, { useState, useCallback } from "react";
import ImageViewer from "react-simple-image-viewer";

export const Gallery = props => {
  const [currentImage, setCurrentImage] = useState(0);
  const [isViewerOpen, setIsViewerOpen] = useState(false);

  const data = [
    { thumb: "img/landing/Match AI App - Login.png", title: "Upcoming App Login" },
    { thumb: "img/landing/Match AI App - Matches.png", title: "Upcoming App Matches" },
    { thumb: "img/landing/Match AI App - Recommendations.png", title: "Upcoming App Recommendations" },
    { thumb: "img/landing/DALL·E 2023-02-06 12.02.09.png", title: "Connecting" },
    { thumb: "img/landing/DALL·E 2023-02-06 13.15.04.png", title: "Connecting" },
    { thumb: "img/landing/MVP Mockup BFF.png", title: "BFF" },
    { thumb: "img/landing/MVP Mockup Career.png", title: "Career" },
    { thumb: "img/landing/MVP Mockup Romance.png", title: "Romance" }
  ];

  const images = data.map(obj => obj.thumb.replace("-small", "-large"));

  const openImageViewer = useCallback(index => {
    setCurrentImage(index);
    setIsViewerOpen(true);
  }, []);

  const closeImageViewer = () => {
    setCurrentImage(0);
    setIsViewerOpen(false);
  };

  return (
    <div id="portfolio" className="text-center">
      <div className="container">
        <div className="section-title">
          <h2 style={{ color: 'white'}}>Gallery</h2>
        </div>
        <div className="row">
          <div className="portfolio-items">
            {data.map(({ title, thumb }, index) => (
              <div key={index} onClick={() => openImageViewer(index)} className="col-sm-6 col-md-4 col-lg-4">
                <div className="portfolio-item cursor">
                  <div className="hover-bg">
                    <div className="hover-text">
                      <h4>{title}</h4>
                    </div>
                    <img src={thumb} className="img-responsive" alt="Project Title" />{" "}
                  </div>
                </div>
              </div>
            ))}
          </div>

          {isViewerOpen && (
            <ImageViewer
              src={images}
              backgroundStyle={{ zIndex: 99999 }}
              currentIndex={currentImage}
              onClose={closeImageViewer}
            />
          )}
        </div>
      </div>
    </div>
  );
};
